.MovieCard {
  background-color: white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align:start;
      align-items:flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom:50px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 20px;
  -webkit-box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133, 133, 133, 022);
          box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133, 133, 133, 022);
}

.MovieCard__Column {
  width: 30%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.MovieCard__Column:last-child {
  padding: 30px 40px 30px 0;
  width: 70%;
}

.MovieCard .MovieCard__Poster {
  width : 90%;
  position : relative;
  top: -20px;
  -webkit-box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
          box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

.MovieCard .MovieCard__sypnosis {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #B4B5BD;
}

@media screen and (min-width:320px) and (max-width:950px) {
  /* Phone & Tablet */
  .MovieCard {
    width: 100%;
  }
}

/* and (orientation:portrait) */
@media screen and (min-width:320px) and (max-width:650px) and (orientation:portrait) {
  .MovieCard {
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .MovieCard__Poster {
    width: 100%!important;
  }

  .MovieCard__Column {
    width:100%!important;
  }

}



